import React, { Fragment } from "react";
import '../navigation/AppNavigator.styles.css';

import ScrollUp from "../components/ScrollUp/ScrollUp";
import Header from "../components/Header/Header";

import MainScreen from "../screens/MainScreen/MainScreen";
import SkillSection from "../screens/SkillScreen/SkillScreen";
import ProjectSection from "../screens/ProjectScreen/ProjectScreen";
import FooScreen from "../screens/FooScreen/FooScreen";

const AppNavigator = () => {
  return (
    <Fragment>
      
      <ScrollUp />
      <Header />

      <MainScreen />
      <SkillSection />
      <ProjectSection />
      <FooScreen />

    </Fragment>

  );
};

export default AppNavigator;