import React from 'react';
import { Container, Row, Col, OverlayTrigger, Tooltip } from "react-bootstrap";
import projectGroups from '../../data/projects.json';
import ParticlesLinks from "../../components/ParticlesLinks";
import "./ProjectScreen.styles.css";

const ProjectCard = ({ project, index }) => {

  const coverAndroid = project.coverAndroid || "./assets/covers/set-android-gray.png";
  const coverIOS = project.coverIOS || "./assets/covers/set-iphone.png";

  const DescBlock = (
    <Col lg={4}>
      <div className="desc-project">
        <p className="img-project-box">
          <img className="img-project" src={project.logo} alt={project.title} />
        </p>
        <p className="desc-app">{project.description}</p>
        <div className="tools-app">
          <p className="badges">
            {project.tags.map((tag, i) => (
              <span key={i} className={`badge badge-${i + 1}`}>{tag}</span>
            ))}
          </p>
        </div>
        <p className="subtitles-app">DEMO</p>
        <p className="share-app">
          {project.linkPlayStore ? (
            project.linkPlayStore.startsWith('http') ? (
              <OverlayTrigger placement="bottom" overlay={<Tooltip>Ver en PlayStore</Tooltip>}>
                <a href={project.linkPlayStore} target="_blank" rel="noopener noreferrer">
                  <img className="share-img" src="./assets/icons/playstore.png" alt="Play Store" />
                </a>
              </OverlayTrigger>
            ) : (
              <OverlayTrigger placement="bottom" overlay={<Tooltip>Descargar App</Tooltip>}>
                <img
                  className="share-img"
                  src="./assets/icons/playstore.png"
                  alt="App Store"
                  style={{ cursor: 'pointer' }}
                  onClick={() => {
                    const link = document.createElement('a');
                    link.href = project.linkPlayStore;
                    link.download = project.linkPlayStore.split('/').pop();
                    document.body.appendChild(link);
                    link.click();
                    document.body.removeChild(link);
                  }}
                />
              </OverlayTrigger>
            )
          ) : (
            <OverlayTrigger placement="bottom" overlay={<Tooltip>No disponible</Tooltip>}>
              <img className="share-img disabled" src="./assets/icons/playstore.png" alt="App Store" style={{ opacity: 0.5 }} />
            </OverlayTrigger>
          )}
          {/*-------------------------------------------------------------------------------------------*/}
          {project.linkAppStore ? (
            project.linkAppStore.startsWith('http') ? (
              <OverlayTrigger placement="bottom" overlay={<Tooltip>Ver en AppStore</Tooltip>}>
                <a href={project.linkAppStore} target="_blank" rel="noopener noreferrer">
                  <img className="share-img" src="./assets/icons/link-appstore.png" alt="App Store" />
                </a>
              </OverlayTrigger>
            ) : (
              <OverlayTrigger placement="bottom" overlay={<Tooltip>Descargar App</Tooltip>}>
                <img
                  className="share-img"
                  src="./assets/icons/link-appstore.png"
                  alt="App Store"
                  style={{ cursor: 'pointer' }}
                  onClick={() => {
                    const link = document.createElement('a');
                    link.href = project.linkAppStore;
                    link.download = project.linkAppStore.split('/').pop();
                    document.body.appendChild(link);
                    link.click();
                    document.body.removeChild(link);
                  }}
                />
              </OverlayTrigger>
            )
          ) : (
            <OverlayTrigger placement="bottom" overlay={<Tooltip>No disponible</Tooltip>}>
              <img className="share-img disabled" src="./assets/icons/link-appstore.png" alt="App Store" style={{ opacity: 0.5 }} />
            </OverlayTrigger>
          )}
        </p>
      </div>
    </Col>
  );

  const IOSBlock = (
    <Col lg={4}>
      <div className="box-ios">
        {project.gifIOS && <img className="video-ios" src={project.gifIOS} alt="iOS demo" />}
        <img className="cover-ios" src={coverIOS} alt="iOS cover" />
      </div>
      <p className="line-app-ios">iOS</p>
    </Col>
  );

  const AndroidBlock = (
    <Col lg={4}>
      <div className="box-android">
        <img className="cover-android" src={coverAndroid} alt="Android cover" />
        {project.gifAndroid && <img className="video-android" src={project.gifAndroid} alt="Android demo" />}
      </div>
      <p className="line-app-android">Android</p>
    </Col>
  );

  // Alternar bloques
  const blocks = index % 2 === 0
    ? [DescBlock, IOSBlock, AndroidBlock]
    : [AndroidBlock, IOSBlock, DescBlock];

  return <Row className="mb-5">{blocks.map((Block, i) => <React.Fragment key={i}>{Block}</React.Fragment>)}</Row>;
};

const ProjectGroup = ({ group, groupIndex }) => {
  return (
    <div id="projects" className="projects-section" >
      <Container fluid="md">
        <Row>
          <Col>
            <div
              style={{
                backgroundColor: 'black',
                height: "200px",
                width: "100%",
                position: "relative",
                overflow: "hidden",
              }}
            >
              <ParticlesLinks id={`particles-projects-${groupIndex}`} />
              <div
                style={{
                  position: "absolute",
                  top: "50%",
                  left: "50%",
                  transform: "translate(-50%, -50%)",
                  color: "white",
                  textAlign: "center",
                  zIndex: 1,
                }}
              >
                <div className="box-projects">
                  <p className="txt-box-years">{group.groupTitle}</p>
                  <p className="txt-box-years-sub">{group.groupPeriod}</p>
                </div>
              </div>
            </div>
          </Col>
        </Row>

        {group.projects.map((project, idx) => (
          <React.Fragment key={idx}>
            <ProjectCard project={project} index={idx} />
          </React.Fragment>
        ))}
      </Container>
    </div>

  );
};

const ProjectScreen = () => {
  return (
    <div>
      {projectGroups.map((group, idx) => (
        <ProjectGroup key={idx} group={group} groupIndex={idx} />
      ))}
    </div>
  );
};

export default ProjectScreen;
