import { Button } from "react-bootstrap";
import '../ScrollUp/ScrollUp.styles.css';

const ScrollUp = () => {

  const handleClick = () => {
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  return (
    <Button className="runToTop" variant="dark" onClick={handleClick}>
      <img className="up-button" src="./assets/icons/up.png" alt="Subir" />
    </Button>
  );
};

export default ScrollUp;