import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import "./FooScreen.styles.css";
import Fab from '@mui/material/Fab';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import DraftsIcon from '@mui/icons-material/Drafts';

const ContactIconButton = ({ icon: Icon, onClick }) => (
  <Fab
    sx={{ width: 80, height: 80 }}
    size="large"
    onClick={onClick}
  >
    <Icon sx={{ fontSize: 40 }} />
  </Fab>
);

const FooScreen = () => {
  const mailTo = 'linkndead@gmail.com';
  const subject = 'Mobile Developer';
  const body = 'Hola Eduardo... ';

  return (
    <div id="contact" className="contact-me-section">
      <Container fluid="md">
        <Row>
          <Col>
            <div className="contact-me-box">
              <p className="txt-contact-me">Contctame</p>
              <div className="icons-bar">
                <ContactIconButton
                  icon={LinkedInIcon}
                  onClick={() => window.open('https://www.linkedin.com/in/eduardohzrz', '_blank', 'noopener,noreferrer')}
                />
                <ContactIconButton
                  icon={DraftsIcon}
                  onClick={() => {
                    window.location.href = `mailto:${mailTo}?subject=${subject}&body=${body}`;
                  }}
                />
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default FooScreen;
