import React, { useState, useEffect, useRef } from "react";
import { Container, Row, Col, Image } from "react-bootstrap";
import ReactiveButton from 'reactive-button';
import TextLoop from "react-text-loop";
import { gsap } from "gsap";

import "./MainScreen.styles.css";
import ParticlesLinks from "../../components/ParticlesLinks";

const MainScreen = () => {
  const name = 'Eduardo Hernndez';
  const career = 'Mobile Developer';
  const [state, setState] = useState('idle');
  const imgRef = useRef(null);

  useEffect(() => {
    gsap.fromTo(
      imgRef.current,
      { opacity: 0, y: -100 },
      { opacity: 1, y: 0, duration: 1.5, ease: "power3.out" }
    );
  }, []);

  const _downloadPDF_local = () => {
    const link = document.createElement('a');
    link.href = '/cv/RN Link CV.pdf';
    link.download = 'CV_ Eduardo Hernandez.pdf';
    link.click();
  };

  const _downloadPDF = async () => {
  try {
    const response = await fetch('https://evgamers.com/cv/download-cv.php');

    if (!response.ok) {
      throw new Error('Error al descargar el archivo');
    }

    const blob = await response.blob();
    const url = window.URL.createObjectURL(blob);
    
    // 1. Descargar el archivo
    const link = document.createElement('a');
    link.href = url;
    link.download = 'CV_Eduardo_Hernandez.pdf';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    
    // 2. Abrir en nueva pestaa
    window.open(url, '_blank');
    
    // 3. Limpiar el URL despus de un tiempo
    setTimeout(() => {
      window.URL.revokeObjectURL(url);
    }, 100);
    
  } catch (error) {
    console.error('Error:', error);
  }
};
  
  const onClickHandler = () => {
    setState('loading');
    setTimeout(() => {
      setState('success');
      _downloadPDF();
    }, 1000);
  };

  return (
    <div id="main" class="main-section">
      <Container fluid="md">
        <Row>
          <Col>
            <div
              style={{
                backgroundColor: 'black',
                height: "500px",
                width: "100%",
                position: "relative",
                overflow: "hidden",
              }}
            >
              <ParticlesLinks id="particles-main" />
              <div
                style={{
                  position: "absolute",
                  top: "50%",
                  left: "50%",
                  transform: "translate(-50%, -50%)",
                  color: "white",
                  textAlign: "center",
                  zIndex: 1,
                }}
              >
                <div className="box-img-w-title">
                  {/*<Image className="img-cv" src="./assets/cv_photo.png" roundedCircle />*/}
                  <Image
                    ref={imgRef}
                    className="img-cv"
                    src="./assets/cv_photo.png"
                    alt="Foto CV"
                    roundedCircle
                  />
                  <div className="box-name">
                    <p className="name-txt">{name + ' | ' + career}</p>
                    <p className="name-txt gigant">
                      <TextLoop springConfig={{ stiffness: 300, damping: 10 }}>
                        <span>
                          <img
                            src={require('../../assets/rn.png')}
                            alt="React Native"
                            style={{ height: 30, marginRight: 12, verticalAlign: 'middle' }}
                          />
                          React Native
                        </span>
                        <span>
                          <img
                            src={require('../../assets/android.png')}
                            alt="Android"
                            style={{ height: 20, marginRight: 12, verticalAlign: 'middle' }}
                          />
                          Android
                        </span>
                        <span>
                          <img
                            src={require('../../assets/ios.png')}
                            alt="iOS"
                            style={{ height: 30, marginRight: 12, verticalAlign: 'middle' }}
                          />
                          iOS
                        </span>
                        <span>
                          <img
                            src={require('../../assets/flutter.png')}
                            alt="Flutter"
                            style={{ height: 30, marginRight: 12, verticalAlign: 'middle' }}
                          />
                          Flutter
                        </span>
                        <span>
                          <img
                            src={require('../../assets/xamarin.png')}
                            alt="Xamarin/MAUI"
                            style={{ height: 30, marginRight: 12, verticalAlign: 'middle' }}
                          />
                          Xamarin/MAUI
                        </span>
                      </TextLoop>
                    </p>
                  </div>

                  <ReactiveButton
                    buttonState={state}
                    idleText="Descargar CV"
                    loadingText="Cargando"
                    successText="Listo"
                    onClick={onClickHandler}
                    style={{ fontSize: 18, padding: 10, letterSpacing: 2 }}
                    color="black"
                    width={200}
                    shadow
                    rounded
                  />

                </div>
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default MainScreen;