import { useCallback } from "react";
import Particles from "react-tsparticles";
import { loadLinksPreset } from "tsparticles-preset-links";

export default function ParticlesLinks({ id = "tsparticles" }) {
  const particlesInit = useCallback(async (engine) => {
    await loadLinksPreset(engine);
  }, []);
  return (
    <Particles
      id={id}
      init={particlesInit}
      options={{
        fullScreen: { enable: false },
        background: {
          color: "transparent",
        },
        particles: {
          number: {
            value: 25, //  menos partculas = menos links
            density: {
              enable: true,
              area: 600, // puedes subir este valor para disminuir densidad visual
            },
          },
          color: {
            value: "#ffffff",
          },
          links: {
            enable: true,
            distance: 130, // ms bajo = ms cercanas
            color: "#ffffff",
            opacity: 0.4,
            width: 1,
          },
          move: {
            enable: true,
            speed: 1.5,
          },
          size: {
            value: { min: 1, max: 3 },
          },
          opacity: {
            value: 0.5,
          },
          shape: {
            type: "circle",
          },
        },
        detectRetina: true,
      }}
      style={{
        position: "absolute",
        width: "100%",
        height: "100%",
        top: 0,
        left: 0,
        pointerEvents: "none",
      }}
    />

  );
}
